import './App.css';
import {Button, Nav, Navbar, Container} from 'react-bootstrap';
import {Routes, Route, useNavigate} from 'react-router-dom';
import {lazy, Suspense, useEffect, useState} from 'react';
import axios from 'axios';

const Login = lazy(() => import('./routes/Login.js'))
const Register = lazy(() => import('./routes/Register.js'))
const Test = lazy(() => import('./routes/Test.js'))
const Statistic = lazy(() => import('./routes/Statistic.js'))

function App() {
  const [loggedIn, setLoggedIn] = useState(false)
  const [username, setUserName] = useState('')
  
  let navigate = useNavigate();

  const isUser = async () => {
    try {
      const res = await axios.get('/check-auth', {withCredentials : true})
      if (res.data.loggedIn) {
        setLoggedIn(true)
        setUserName(res.data.user.username)
      } else {
        setLoggedIn(false)
        setUserName('')
      }
    } catch (error) {
      console.log('Error: ', error)
    }
  }
  
  useEffect(() => {
    isUser()
  }, [])

  return (
    <div className="App">
      <Navbar expand="lg" className="bg-body-tertiary">
        <Container>
          <Navbar.Brand onClick={() => {navigate('/')}}>  </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Item>
                <Nav.Link onClick={() => {
                  loggedIn ? 
                  navigate('/test')
                  :
                  alert("  .")
                }}></Nav.Link>
              </Nav.Item>
              {loggedIn && 
              <Nav.Item>
                <Nav.Link onClick={() => {navigate('/statistic')}}></Nav.Link>
              </Nav.Item>}
            </Nav>
            <Nav className="ml-auto">
              {loggedIn ? 
              <div style={{display: 'flex', justifyContent: 'space-between'}}>
                <Nav.Item className='me-3 mt-1'>
                  {username + ', .'}
                </Nav.Item>
                <Nav.Item>
                  <Button onClick={async () => {
                    await axios.get('/logout', {withCredentials : true})
                      .then(response => {
                        if (response.status == 200) {
                          window.location.href = '/'
                        }
                      })
                      .catch(error => {
                        alert(error)
                      })
                  }}></Button>
                </Nav.Item>
              </div>
              :
              <div style={{display: 'flex', justifyContent: 'space-between'}}>
                <Nav.Item>
                  <Nav.Link onClick={() => {navigate('/login')}}></Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link onClick={() => {navigate('/register')}}></Nav.Link>
                </Nav.Item>
              </div>}
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Routes>
        <Route path='/' element={
          <>
            <div>
                .
            </div>
          </>
        } />
        <Route path='/test' element={
          <Suspense fallback={<div></div>}>
            <Test />
          </Suspense>
        }/>
        <Route path='/login' element={
          <Suspense fallback={<div></div>}>
            <Login />
          </Suspense>
        }/>
        <Route path='/register' element={
          <Suspense fallback={<div></div>}>
            <Register />
          </Suspense>
        }/>
        <Route path='/statistic' element={
          <Suspense fallback={<div></div>}>
            <Statistic />
          </Suspense>
        }/>
      </Routes>
    </div>
  );
}

export default App;
